import React from 'react';
import emailjs from "emailjs-com";

import "./HireMe.css";

function HireMe() {

    function sendEmail (e) {
        e.preventDefault();

        emailjs.sendForm("gmail", "template_lkqrvsh", e.target, "user_JUVxhcLuVzF3gRBltflLy")
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
    }
    return (
        <div className="hireMe" id="hireMe">
            <h1>Hire Me</h1>
            <form onSubmit={ sendEmail }>
    
                <input name="name" placeholder="Enter your name" className="hireMe__styles"></input>
                <input name="email" placeholder="Enter your email address" className="hireMe__styles"></input>
                <input name="subject" placeholder="Subject" className="hireMe__styles"></input>
                <textarea name="message" placeholder="Type your message..." className="hireMe__styles"></textarea>
            <button value="Send Message">
                Submit
            </button>              
            </form>
    
        </div>
    )
}

export default HireMe;
