import React from 'react';

import ProjectCard from "./ProjectCard/ProjectCard";
import "./Projects.css";

function Projects() {
    return (
        <div id="projects">
            <h1 className="projects__title">Projects</h1>
            <div className="projects__container">
            
            <div className="item">
            <ProjectCard title="Covid-19T" subheader="A Coronavirus tracker app" description="Covid-19T pulls the data from API and displays infected, recovered and death cases." tools="CSS3, ReactJS" deployed="GitHub Pages" webLink="https://imviharevankar.github.io/covid19t/" gitHubLink="https://github.com/imviharevankar/covid19t" /> 
            </div>

            <div className="item">
            <ProjectCard title="Portfolio" subheader="" description="A portfolio displays all the project work and contact info." tools="CSS3, ReactJS" deployed="GitHub Pages" webLink="https://imviharevankar.github.io/portfolio/" gitHubLink="https://github.com/imviharevankar/portfolio"  />  
            </div>

            <div className="item">
            <ProjectCard title="Divisa" subheader="A Forex Calculator" description="Divisa gives the exchange rate between all the listed currencies" tools="HTML5, CSS3, JS" deployed="GitHub Pages" webLink="https://imviharevankar.github.io/divisas/" gitHubLink="https://github.com/imviharevankar/divisas"  /> 
            </div>

            <div className="item">
            <ProjectCard title="Clima" subheader="A Weather App" description="Clima gives the weather details of the user based on his location." tools="HTML5, CSS3, JS" deployed="GitHub Pages" webLink="https://imviharevankar.github.io/clima/" gitHubLink="https://github.com/imviharevankar/clima"  />  
            </div>
            
            <div className="item">
            <ProjectCard title="TinDog" subheader="A Tinder for Dogs" description="A landing page designed for Tinder for Dogs" tools="HTML5, CSS3, BootStrap4" deployed="GitHub Pages" webLink="https://imviharevankar.github.io/tindog/" gitHubLink="https://github.com/imviharevankar/tindog"  /> 
            </div>

            </div>
            
        </div>
    )
}

export default Projects;
